body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

}
*{
  padding:0;margin:0;
}
html,body,#root{
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.App {
  text-align: center;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #ffffff;
  height: 64px;
  color: white;
  width: 100%;
  -ms-flex: 0 0;
      flex: 0 0
}
.App-main{
  width: 100%;
  min-height: 500px;
  -ms-flex: 0 0 1;
      flex: 0 0 1
}
.App-footer{
  height: 100px;
  background:rgb(33,33,33);
  color: aliceblue;
  width: 100%;
  font-size: 14px;
  line-height: 100px;
  -ms-flex: 0 0;
      flex: 0 0
}
.App-footer a{
  cursor: pointer;
}
.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



